<template>
  <div class="news" style="width: 100%">
    <div id="menu">
      <ul>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
        <li :class="{ active1: tabName == ''}" @click="setTab('')"></li>
      </ul>
    </div>
    <news-tab :tab="tabName"></news-tab>
    <router-view></router-view>
    <div class="sidebar">
      <div class="back-top"><i class="backtop" style="display: none"></i></div>
    </div>
  </div>
</template>
<script>
// axios
import Jquery from './jquery.min.js'
import NewsTab from './NewsTab/NewsTab.vue'
export default {
  data () {
    return {
      tabName: '',
      age: 18,
      toc: [],
      tabs: {
        toutiao: '',
        tiyu: '',
        shehui: '',
        guoji: '',
        guonei: '',
        yule: ''
      }
    }
  },
  methods: {
    setTab (tabName) {
      this.tabName = tabName
    }
  },
  created () {
  },
  components: {
    NewsTab
  }
}
$(function () {
  // 0
  var index = 0;
  // 
  var len = $('.carousel-image li').length;
  // 
  var timer;
  // 
  $('.carousel-image li:first').clone(true).appendTo($('.carousel-image'));
  run();

  // 1.
  function run() {
    timer = setInterval(function () {
      //  console.log('len='+len+',index='+index)
      $('.carousel-index li:eq(' + index%len + ')').removeClass('active');

      // 
      if (index == len) {
        // margin-left0
        $('.carousel-image').css('margin-left', 0);
        // 
        index =0;
      }
      index++;

//      console.log(index);
      $('.carousel-index li:eq(' + index%len + ')').addClass('active');
      $('.carousel-image').animate({marginLeft:-375*index}, 200);
    }, 2000);
  }

  // 2. 
  $('.carousel').mouseenter(function () {
    // 
    clearInterval(timer);
  }).mouseleave(function () {
    // 
    run();
  })

  // 3.
  $('.carousel-index li').mouseenter(function () {
    $('.carousel-index li:eq(' + index + ')').removeClass('active');

    index = $(this).index();

    $('.carousel-index li:eq(' + index + ')').addClass('active');
    $('.carousel-image').stop(true).animate({marginLeft:-375*index}, 200);
  })
})
//
$(function () {
  $(window).scroll(function () {
    if ($(this).scrollTop() > 510) {
      $('.backtop').show();
    } else {
      $('.backtop').hide();
    }
  });


  $('.backtop').click(function () {
    var top = $(window).scrollTop();
    var timer = setInterval(function () {
      top -= 10;

      if (top < 10) {
        top = 0;
        clearInterval(timer)
      }
      $(window).scrollTop(top)
    }, 1)
  })
})
</script>
<style lang="styl">
  .sidebar {
    position: fixed;
    z-index: 10;
    right: 0.3125rem;
    bottom: 6.25rem;
    width: 2.8125rem;
    padding: 0.3125rem 0.625rem;
    line-height: 1.5;
    font-size: 0.75rem;
    color: #666;
  }
  .back-top i {
    display: inline-block;
    width: 0.9375rem;
    height: 0.5rem;
    background-size: 13.125rem 10.9375rem;
    position: absolute;
    bottom: -1.25rem;
    right: 1.25rem;
    background: url(../assets/images/match-icon.png) no-repeat -9.9375rem -4.375rem;
  }

  #menu
    position fixed
    top 3.15625rem
    width 100%
    height 2.5rem
    background #5dabf0
    z-index 2
    ul
      width 100%
      height 100%
      display flex
      align-items center
      justify-content space-around
      li
        padding 0.1875rem 0.25rem
        color #fff
        font-size 0.875rem
        border-radius 0.1875rem
        &.active1
          background #369219
          color #FFF
  .carousel
    overflow hidden
    position relative
    width 23.4375rem
    height 17.5625rem
    margin 0.125rem auto
    .carousel-image
      display block
      li
        float left
        position relative
      img
        width 23.4375rem
        vertical-align middle
        display block
        min-height 17.4375rem
      p
        position absolute
        text-align left
        height 2.5rem
        font-size 1rem
        color #fff
        bottom 0
        left 0.625rem
        width 100%
        box-sizing border-box
          span
            display inline-block
            text-align left
            height 2.0rem
            line-height 2.0rem
            width 100%
            position absolute
            bottom 0
            font-size 1rem
    div
      position absolute
      top 9.375rem
      width 3.75rem
      height 3.75rem
      border-radius 50%
      line-height 3.75rem
      text-align center
      background yellow
    .carousel-index
      position absolute
      bottom 0.625rem
      right 0.625rem
      li
        float left
        width 0.625rem
        height 0.625rem
        margin-left 0.3125rem
        text-align center
        line-height 1.125rem
        background #ddd
        border-radius 50%
        &.active
          background #393e3e
</style>



