<template>
  <div id="index">
    <div id="list-content">
      <div id="menu">
        <ul>
          <li :class="{ active: tabName == 'all' }" @click="setTab('all')"></li>
          <li :class="{ active: tabName == 'good' }" @click="setTab('good')"></li>
          <li :class="{ active: tabName == 'weex' }" @click="setTab('weex')">weex</li>
          <li :class="{ active: tabName == 'share' }" @click="setTab('share')"></li>
          <li :class="{ active: tabName == 'ask' }" @click="setTab('ask')"></li>
          <li :class="{ active: tabName == 'job' }" @click="setTab('job')"></li>
        </ul>
      </div>
      <!---->
      <!--<loading v-if="!topics.length"></loading>-->
      <!--  -->
      <!--<div :is="tabName" :tab="'all'"></div>-->
      <tab :tab="tabName"></tab>

      <router-view></router-view>

    </div>
  </div>
</template>

<script>
// axios
import Tab from './tab/Tab'

export default {
  data () {
    return {
      title: '',

      // 
      topics: [],

      // name
      tabName: ''
    }
  },

  // 
  methods: {
    // name
    setTab (tabName) {
      this.tabName = tabName
    }
  },

  // 
  created () {
  },
  components: {
    Tab
  }
}
</script>

<style scope lang="styl">
  #index
    width 100%
    box-sizing border-box
    #list-content
      background #FFF
      border-radius 0.1875rem
      min-height 41.625rem
      #menu
        position fixed
        top 3.125rem
        width 100%
        height 2.5rem
        background #f6f6f6
        ul
          width 100%
          height 100%
          display flex
          align-items center
          justify-content space-around
          li
            padding 0.1875rem 0.25rem
            color #369219
            font-size 0.875rem
            border-radius 0.1875rem
            &.active
              background #369219
              color #FFF
      .top-enter
        opacity 0
      .top-enter-active
        transition opacity 0.3s linear
      .top-enter-to
        opacity 1
      .top-leave
        opacity 1
      .top-leave-active
        transition opacity 0.3s linear
      .top-leave-to
        opacity 0
</style>



