<template>
  <!-- list-item -->
  <div id="list-item" v-if="topics.length">
    <div id="item" v-bind:key="index" v-for="(item, index) in topics">
      <div class="user-pic">
        <!-- <img src="../assets/imgs/user-pic.jpg" alt=""> -->
        <!--  -->
        <img v-if="item.author" v-lazy="item.author.avatar_url" alt="">

        <!-- v-lazyerror -->
        <!-- <img v-lazy="'1.jpg'" alt="" > -->
      </div>
      <div class="state" :class="{toporgood: item.top || item.good}">
        <!--
          
              top
              good
             tab
        -->
        <span v-if="item.top"></span>
        <span v-else-if="item.good"></span>
        <span v-else>
          {{ tabs[item.tab] }}
        </span>
      </div>
      <div class="topicInfo">
        <router-link :to="'/index/topic/'+item.id" class="title">{{ item.title }}</router-link>
        <span class="num">{{ item.reply_count }}/{{ item.visit_count }}</span>
      </div>
      <div class="time">
        <!--  -->
          {{ setTimeAgo(item.last_reply_at || item.create_at) }}
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
// 
import timeago from 'timeago.js'

export default {
  data () {
    return {
      tabs: {
        ask: '',
        job: '',
        share: '',
        weex: 'weex'
      }
    }
  },
  // 
  props: ['topics'],

  methods: {
    setTimeAgo (time) {
      let setTime = timeago()
      return setTime.format(time, 'zh_CN')
    }
  }
}
</script>
<style scope lang="styl">
#list-item
  width 100%
  height auto
  padding 0.1875rem 0.5rem
  box-sizing border-box
  #item
    width 100%
    height 3.125rem
    /*margin 0.25rem 8px*/
    display flex
    align-items center
    .user-pic
      width 1.875rem
      height 1.875rem
      border-radius 0.1875rem
      overflow hidden
      img
        display block
        width 100%
        height 100%
    .state
      padding 0.1875rem 0.25rem
      background #e5e5e5
      color #999
      font-size 0.75rem
      border-radius 0.1875rem
      margin-left 0.1875rem
    .toporgood
      background #369219
      color #FFF
    .topicInfo
      width 14.875rem
      height 100%
      display flex
      flex-direction column
      justify-content center
      /*background red*/
      margin-left 0.3125rem
      font-size 0.875rem
      .title
        /*  */
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        color #333
      .num
        font-size 0.75rem
        color #b4b4b4
    .time
      font-size 0.75rem
      text-align center
      color #778087
      margin-left 0.1875rem
</style>



