<template>
  <div id="tab">
    <!-- loading -->
    <loading v-if="!news.length"></loading>
    <!--<news-top :newsList="news"></news-top>-->
    <news-list :newsList="news"></news-list>
  </div>
</template>
<script>
import Loading from '../Loading'
import NewsList from './NewsList'
import NewsTop from './NewsTop'
import { getNewsList } from './getData/jsonpGetData.js'

export default {
  data () {
    // 
    return {
      news: [],
      img:[]
    }
  },
  // 
  components: {
    Loading,
    NewsList,
    NewsTop
  },
  props: ['tab'],
  methods: {
    // 
    _getNewsList (tab) {
      getNewsList({
        // 
        channel: tab
      }).then((data) => {
        this.news = data.result.list
        this.img = data.result.list
        console.log(this.img)
      })
    }
  },

  // -createdVue
  created () {
    // 
    this._getNewsList(this.tab)
  },

  // 
  watch: {
    tab () {
      // tab
      this._getNewsList(this.tab)
    }
  }
}

</script>
<style></style>



